/* version 1.0.2 */
/* 見たまま編集バリデーション */
[data-element-id] .up-relative\@edit {
  position: relative;
}
[data-element-id] .up-absolute\@edit {
  position: absolute;
}
[data-element-id] .up-spmark_edit {
  border: solid 1px #000;
}
[data-element-id] .up-spmark_edit:before {
  content: "スマホ時のみ表示";
  display: block;
  position: relative;
  z-index: 2;
  font-size: 12px;
  color: #fff;
  background: #000;
}
[data-element-id] .up-none {
  display: inherit;
}
[data-element-id] [class*="heightLine"] {
  height: auto !important;
}
[data-element-id] .fead-mv {
  opacity: 1;
}
[data-element-id] .fead-up {
  opacity: 1;
  transform: translateY(0);
}
[data-element-id] .fead-left {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
[data-element-id] .fead-right {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
[data-element-id] .imgalt {
  position: relative;
  z-index: 50;
  overflow: hidden;
  padding: 2px 4px;
  max-height: 70px;
  background: #333;
  color: #ffa500;
  text-align: left;
  font-size: 14px;
}
[data-element-id] .altcolor {
  color: #f00;
}
[data-common-area] {
  position: relative;
}
[data-common-area]:hover:before {
  position: absolute !important;
  bottom: 100%;
  left: 50%;
  box-sizing: border-box;
  padding: 5px;
  min-width: 200px;
  width: 100%;
  background: #555;
  color: #fdc44f;
  content: "共通要素です。";
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  transform: translateX(-50%);
}
a[data-href="#"] {
  position: relative !important;
  display: block;
  border: solid #f00 5px !important;
  opacity: 1;
}
a[data-href="#"]:hover:before {
  position: absolute !important;
  bottom: 100%;
  left: 50%;
  box-sizing: border-box;
  padding: 5px;
  min-width: 200px;
  width: 100%;
  background: #555;
  color: #f00;
  content: "リンクが設定されていない可能性があります。";
  font-weight: bold;
  font-size: 12px;
  transform: translateX(-50%);
}
a[data-href*="."] {
  position: relative !important;
  display: block;
  border: solid #f00 5px !important;
  opacity: 1;
}
a[data-href*="."]:hover:before {
  position: absolute !important;
  bottom: 100%;
  left: 50%;
  box-sizing: border-box;
  padding: 5px;
  min-width: 200px;
  width: 100%;
  background: #555;
  color: #f00;
  content: "外部リンクの可能性があります。リンクが飛ばない可能性がありますので注意してください。";
  font-weight: bold;
  font-size: 12px;
  transform: translateX(-50%);
}
[data-element-id][data-uk-slideshow]:hover:before {
  position: absolute;
  z-index: 1000;
  display: block;
  clear: both;
  padding: 5px;
  width: 100%;
  background: #4c4c4c;
  color: #f00;
  content: "※こちらは見たまま編集ができません。①画像をダブルクリック、②変更（歯車マーク）、③スライドショーから編集してください。";
  text-align: left;
  letter-spacing: 1px;
  font-weight: bold;
  font-size: 12px;
  line-height: 1.8;
}
[data-collection-list-content]:hover:before {
  position: absolute;
  z-index: 1000;
  display: block;
  clear: both;
  padding: 5px;
  width: 100%;
  background: #4c4c4c;
  color: #f00;
  content: "※こちらは見たまま編集ができません。①ページのどこかをダブルクリック、②左下（もしくは左上）のCMSロゴをクリック、③画面右上のメニュー（三本線マーク）から「カスタムコンテンツ管理」をクリック、④編集したいカスタムコンテンツの一覧（三本線マーク）から記事投稿・記事編集ができます。";
  text-align: left;
  letter-spacing: 1px;
  font-weight: bold;
  font-size: 12px;
  line-height: 1.8;
}
